import React from "react";

function Contact() {
  return (
    <section id="showcase">
      <div className="contact-title">
        <h1>Say hello</h1>
        <h3>I am always here to listen</h3>
      </div>
      <form className="contact-form">
        <input
          name="name"
          type="text"
          className="form-control"
          placeholder="Your Name"
          required
        />
        <br />
        <input
          name="email"
          type="email"
          className="form-control"
          placeholder="Your Email"
          required
        />
        <br />
        <textarea
          name="message"
          className="form-control"
          placeholder="Message"
          row="8"
        ></textarea>
        <br />
        <input
          type="submit"
          className="form-control submit"
          value="SEND MESSAGE"
        />
      </form>
    </section>
  );
}

export default Contact;
