import React from "react";
import Navbar from "./Navbar";

function Header() {
  return (
    <header id="header">
      <div className="container">
        <div id="branding">
          <h1>
            <span class="highlight">Chris'</span> Space
          </h1>
        </div>
        <Navbar />
      </div>
    </header>
  );
}

export default Header;
