import React, { useState, useEffect } from "react";

function Home() {
  const [dt, setDt] = useState(new Date().toLocaleString());

  useEffect(() => {
    let secTimer = setInterval(() => {
      setDt(new Date().toLocaleString());
    }, 1000);
    return () => clearInterval(secTimer);
  }, []);
  return (
    <section id="showcase">
      <div className="welcome">
        <h2>{dt}</h2>
        <br />
        <h1></h1>
      </div>
    </section>
  );
}

export default Home;
